(function () {
    'use strict';
    var NI_SUPPORT = NationalInstruments.HtmlVI.NISupport;

    // i18n framework in the webcharts calls registerElements when the localized
    // messages are loaded (and sets the NI_SUPPORT.i18n at the same time).
    // It might happen than legends are loaded later, and in this case we still need to register them.
    if (typeof NI_SUPPORT.i18n === 'function') {
        window.JQX.Elements.registerElements();
    }
}());
